import { Axios } from 'axios'
import { uriFy } from '../core'
import { WalletBlocksSearchQuery } from '../wallet-blocks'
import {
  WalletLatestBlockRoutes,
  WalletLatestBlocksClient,
} from './wallet-latest-block.model'

/**
 * Create wallets latest blocks client
 * @param axios - The Axios instance
 * @returns the wallets latest blocks GOT client
 */
export function createWalletLatestBlocksClient(
  axios: Axios
): WalletLatestBlocksClient {
  return {
    search: (searchParams?: WalletBlocksSearchQuery) =>
      axios
        .request({
          url: WalletLatestBlockRoutes.v1Search,
          method: 'GET',
          params: searchParams
            ? new URLSearchParams(uriFy(searchParams))
            : undefined,
        })
        .then((response) => response.data),
    list: (searchParams?: WalletBlocksSearchQuery) =>
      axios
        .request({
          url: WalletLatestBlockRoutes.v1Search,
          method: 'GET',
          params: searchParams
            ? new URLSearchParams(uriFy(searchParams))
            : undefined,
        })
        .then((response) => {
          const page = response.data
          return page.data
        }),
    findOne: (searchParams?: WalletBlocksSearchQuery) =>
      axios
        .request({
          url: WalletLatestBlockRoutes.v1Search,
          method: 'GET',
          params: searchParams
            ? new URLSearchParams(uriFy(searchParams))
            : undefined,
        })
        .then((response) => {
          const page = response.data
          return page.data ? page.data[0] : undefined
        }),
  }
}
